import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import About from "./views/About.vue";
import Otlob from "./views/Otlob.vue";
import Facs from "./views/Facs.vue";
import Contact from "./views/Contact.vue";
import OppsPage from "./views/OppsPage.vue";

Vue.use(Router);

const routes = [
    {
        path: "/",
        name: "home",
        component: Home
    },
    {
        path: "/about",
        name: "About",
        component: About
    },
    {
        path: "/otlob",
        name: "Otlob",
        component: Otlob
    },
    {
        path: "/facs",
        name: "facs",
        component: Facs
    },
    {
        path: "/contact",
        name: "Contact",
        component: Contact
    },
    {
        path: "/opps",
        name: "OppsPage",
        component: OppsPage
    },
    {
        path: "*",
        redirect: OppsPage
    }
];
export default new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes
});
